html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: #e5e5e5;
}

.container{
  margin: 0 auto;
  padding: 50px;
}
.wrapper{
  margin: 0 auto;
  max-width: 1200px;
  border: 1px solid #000;
  padding: 50px;
  justify-content: center;
  text-align: center;
}

.button{
  margin: 0 auto;
  padding: 10px 14px;
  width: 100%;
  cursor: pointer;
  background-color: #b50101;
  transition: background-color 0.6s ease-in-out;
  border: none;
  color: #fff;
}
.button:hover{
  background-color: #ff0000;
}
.testo-down{
  font-size: 20px;
  font-family: Verdana, Arial, Helvetica, sans-serif;
  line-height: 160%;
}
.testo-down::after{
  content: '\2193';
  width: 50px;
  height: 50px;
  margin-left: 10px;
  margin-top: -10px;
}
.testo-up{
  font-size: 20px;
  font-family: Verdana, Arial, Helvetica, sans-serif;
  line-height: 160%;
}
.testo-up::after{
  content: '\2190';
  width: 50px;
  height: 50px;
  margin-left: 10px;
  margin-top: -10px;
}
